body {
  background-color: #eaeaea !important;
}

a,
a:hover,
a:visited,
a:active,
a:link {
  color: inherit;
  text-decoration: none;
}

.positive--custom {
  color: white !important;
  background-color: rgb(49, 132, 196) !important;
}

.positive--custom:hover {
  background-color: rgb(0, 110, 194) !important;
}

.positive--custom--inverted {
  color: rgb(49, 132, 196) !important;
  background-color: white !important;
  border: 2px solid rgb(49, 132, 196) !important;
  transition: background-color 0.1s ease, color 0.1s ease;
}

.positive--custom--inverted:hover {
  color: white !important;
  background-color: rgb(49, 132, 196) !important;
  transition: background-color 0.1s linear, color 0.1s linear;
}

.ui.inverted.top.fixed.menu {
  background-image: linear-gradient(
    135deg,
    rgb(24, 42, 115) 0%,
    rgb(33, 138, 174) 69%,
    rgb(32, 167, 172) 89%
  ) !important;
}

.masthead {
  display: flex;
  align-items: center;
  background-image: linear-gradient(
    135deg,
    rgb(24, 42, 115) 0%,
    rgb(33, 138, 174) 69%,
    rgb(32, 167, 172) 89%
  ) !important;
  height: 100vh;
}

.masthead .ui.menu .ui.button,
.ui.menu a.ui.inverted.button {
  margin-left: 0.5em;
}

.masthead h1.ui.header {
  font-size: 4em;
  font-weight: normal;
}

.masthead h2 {
  font-size: 1.7em;
  font-weight: normal;
}

.profile-image--icon.ui.button {
  position: absolute !important;
  top: 0%;
  right: 0%;
  font-size: 0.9em;
  background-color: rgba(255, 255, 255, 0.95);
  margin: 0;
  border-radius: 0;
  border-bottom-left-radius: 15%;
}

.modal .close.icon {
  position: absolute;
  top: 0;
  right: 0;
  filter: invert(100%);
}

.ui.dropdown.navbar-profile--dropdown {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  padding-left: 4em;
  width: max-content;
}

.navbar--menu.ui.menu .item:before {
  width: 0;
}

.ui .skillList-skillButton.disabled {
  opacity: 1 !important;
  background-color: rgb(255, 255, 255);
  border: 2px solid rgb(133, 183, 217);
  box-shadow: inset 0 0 2px 0px rgb(133, 183, 217),
    0 0 5px -1px rgb(133, 183, 217) !important;
  outline: 0;
}

.ui.skillList-container {
  padding: 0.7em;
  padding-bottom: 0.3em;
}

.ui.skillList-container .skillList-skillButton {
  margin-right: 0.4em;
  margin-bottom: 0.4em;
  opacity: 1 !important;
  background-color: rgb(255, 255, 255);
  outline: 1px solid rgb(133, 183, 217);
  transition: outline linear 0.04s, border linea 0.04s;
}

.ui.skillList-container .skillList-skillButton:hover {
  /* background-color: rgba(145, 145, 145, 0.05) !important; */
  outline: 2px solid rgb(133, 183, 217);
  transition: outline linear 0.04s;
}

.profileSkills-skillContainer.extra .profileSkills-skillLabel {
  background-color: rgb(255, 255, 255);
  outline: 1px solid rgb(133, 183, 217);
}
.ui.search > .results.transition {
  overflow-y: auto;
  max-height: 500px !important;
}

.becomeExpert--skillLabel__hover.ui.label:hover {
  animation: deleteColor 0.25s forwards;
}

@keyframes deleteColor {
  100% {
    color: white;
    background-color: rgba(219, 40, 40, 0.9);
  }
}

.ui.segment.becomeExpert--addPlaceholder__hover:hover {
  background-color: #f9fafb;
  transition: background-color 0.12s linear;
}

.becomeExpert-skillsSegment__noSkills {
  box-shadow: 0px 0px 6px 1px rgb(219, 40, 40) !important;
  background-repeat: no-repeat;
  background-color: white !important;
  /* background-color: rgba(219, 40, 40, 0.02)!important; */
}

.asAButton:hover {
  cursor: pointer;
}

.file-thumbnail-container {
  display: flex;
  flex-wrap: wrap;
  margin-right: 30px;
  margin-bottom: 10px;
  max-width: 150px;
  max-height: 212px;
  height: auto;
  word-wrap: break-word;
  overflow: hidden;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0 0.28571429rem 0.28571429rem;
}

.file-thumbnail-card {
  display: block;
  max-width: 150px;
  max-height: 212px;
  margin-bottom: 6px;
  overflow: hidden;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.3s ease;
  background-color: rgba(80, 80, 80, 0.6);
}

.file-thumbnail-card:hover .overlay {
  opacity: 1;
}

.portfolioItem-nameHeader {
  font-size: 2em;
  padding: 15px 10px;
  box-shadow: 0px -2px 3px -2px #888;
}

.modal.modal-file-preview {
  width: auto;
}
.modal.modal-file-preview .content {
  padding: 0;
}

.ui.buttons.pdfNavigation-buttons {
  position: absolute;
  left: 50%;
  bottom: 5%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity ease-in-out 0.2s;
  border-radius: 4px;
  background-color: white;
}

.ui.buttons.pdfNavigation-buttons .ui.button,
.ui.buttons.pdfNavigation-buttons .ui.label {
  background-color: white;
}

.react-pdf__Document:hover .pdfNavigation-buttons {
  opacity: 1;
}

.react-date-picker,
.react-date-picker__wrapper {
  background-color: white;
  font-size: 1em;
  border-radius: 0.28571429rem;
  /* overflow: hidden; */
}
.react-date-picker__wrapper {
  padding: 0.5em 0.8em;
  border-radius: 0.28571429rem;
  border: 1px solid rgba(34, 36, 38, 0.15) !important;
  color: rgba(0, 0, 0, 0.87);
  vertical-align: revert !important;
  transition: color 0.1s ease, border-color 0.1s ease;
}

.react-date-picker__wrapper:focus-within {
  border: 1px solid rgb(133, 183, 217) !important;
}

.react-date-picker input {
  margin: 0 1px 5px 1px !important;
  padding: 0px !important;
  border: none !important;
  /* overflow: hidden !important; */
  vertical-align: revert !important;
}

.becomeExpert-progressButton {
  /* margin-top: 2em !important; */
  margin-right: -1rem !important;
}

.container input {
  border: none;
  padding: 10px;
  border-radius: 5px;
}
.container.has-focus {
  border: none;
}

.container__select {
  border: none;
}

.container.has-focus .container__value input {
  border: none;
  border: 2px solid yellow;
}

.container.is-selected input {
  border: none;
}

.listSort-dropdown {
  color: #00b5ad !important;
}

.jobListItem-deleteButton {
  position: absolute !important;
  top: 5px;
  right: 5px;
}

.jobListItem-confirmDeleteModal .primary.button {
  background-color: #db2828 !important;
}

.jobListItem-confirmDeleteModal .primary.button:hover {
  background-color: rgb(255, 19, 19) !important;
}

.accountLink-input__disabled.field,
.accountLink-input__disabled.field .disabled.field,
.accountLink-input__disabled.field .input.disabled,
.accountLink-input__disabled .input.disabled input {
  opacity: 0.85 !important;
}

.loading--custom:after {
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
  -webkit-animation: ellipsis steps(4, end) 900ms infinite;
  animation: ellipsis steps(4, end) 900ms infinite;
  content: "\2026"; /* ascii code for the ellipsis character */
  width: 0px;
}

@keyframes ellipsis {
  to {
    width: 20px;
  }
}

@-webkit-keyframes ellipsis {
  to {
    width: 20px;
  }
}

.ui.segments.profileContent-container > .segment .items > .item,
.ui.segment.editProfile-container .items > .item {
  margin-bottom: 1em;
}

.ui.segments.profileContent-container > .segment .items > .item:last-child,
.ui.segment.editProfile-container .items > .item:last-child {
  margin-bottom: 0;
}
